/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import { injectIntl } from 'react-intl';
import {
  Breadcrumb,
  Row,
  Col,
  Button,
  Upload,
  Icon,
  Form,
  message,
  Select,
  Alert,
  Modal,
  Radio,
  Input,
  Divider,
  Typography,
} from 'antd';

import styled from 'styled-components';
import { ButtonWrapper, ColWrapper, FormItemWrapper, Div, DivError, DivButtons } from './../Styled';

import SuccessCmp from './../SuccessCmp';
import ErrorCmp from './../ErrorCmp';
import ProcessList from './../ProcessList';

import functions from './../../utils/functions';

import { processFile, quoteFile, translate } from './../../services';

import './index.css';

const Dragger = Upload.Dragger;
const Option = Select.Option;
const { TextArea } = Input;
const { Paragraph } = Typography;

require('./file-download');

const ModalWrapper = styled(Modal)`
  & {
    .ant-modal-body {
      max-height: 410px;
      padding: 10px 20px 10px 20px;
      margin-bottom: 12px;
      overflow: auto;
    }
  }
`;

const ProcessServicesCmp = props => {
  const {
    intl: { messages },
    userLogin,
    newProcess,
    process: { engines },
    defaultValue,
  } = props;

  const [visible, setVisible] = useState(false);
  const [radioSelect, setRadioSelect] = useState(1); // 0 Upload File 1 Translate
  const [view, setView] = useState('list');
  const [viewNoty, setViewNoty] = useState('process');
  const [fileList, setFileList] = useState([]);
  const [uploading, setUploading] = useState(false);
  const [translating, setTranslating] = useState(false);
  const [result, setResult] = useState('Translation');
  const [processEnd, setProcessEnd] = useState(false);
  const [showError, setShowError] = useState(false);
  const [source, setSource] = useState(null);
  const [target, setTarget] = useState(null);
  const [defaultSourceValue, setDefaultSourceValue] = useState(null);
  const [defaultTargetValue, setDefaultTargetValue] = useState(null);

  useEffect(() => {
    if (newProcess) {
      changeView('process');
    }
  }, []);

  useEffect(() => {
    if (engines.length) {
      changeView('process');
    }
  }, [defaultValue]);

  const initStateView = view => {
    setView(view);
    setViewNoty('process');
    setFileList([]);
    setUploading(false);
    setProcessEnd(false);
    setShowError(false);
  };

  const changeView = view => {
    if (view === 'process') {
      initSelected();
    }
    setView(view);
  };

  const initSelected = () => {
    const {
      selectProcess,
      process: { engines },
    } = props;

    let selected = null;
    let array = [];

    if (engines && engines.length) {
      engines.forEach(element => {
        if (props.defaultValue && element.name === props.defaultValue.toLowerCase()) {
          selectProcess(element);
          selected = element;
        }
      });

      if (selected) {
        if (selected.engines && selected.engines.length) {
          selected.engines.forEach(element => {
            let flag = false;
            for (let i = 0; i < array.length; i++) {
              if (array[i].key === element.source) {
                flag = true;
                break;
              }
            }
            if (!flag) {
              let key = element.source;
              array.push({
                key,
                id: element.id,
                name: functions.languages[key],
                target: element.target,
                source: element.source,
              });
            }
          });
          if (array.length) {
            setDefaultSourceValue(array[0].name);
            setSource(array[0].source);
            setTarget(`${array[0].id}-${array[0].target}`);
            setDefaultTargetValue(getTargets(selected.engines, array[0].source)[0].text);
          }
        }
      }
    } else {
      // message.warning("You don't have engines assigned !");

      changeView('list');
    }
  };

  const handleSubmit = e => {
    e.preventDefault();
    const {
      typeOfUser,
      changeFileID,
      process: { selected },
    } = props;

    const targets = getTargets();

    // eslint-disable-next-line array-callback-return
    const nameEng = targets.filter(item => {
      if (item && item.key === target) {
        return item;
      }
    })[0];

    if (nameEng) {
      setTarget(target);
    }

    setShowError(false);
    setViewNoty('process');

    props.form.validateFields(async (err, values) => {
      if (!err) {
        let aux = values.target.split('-');
        if (radioSelect === 1) {
          if (fileList.length) {
            const promises = fileList.map(item => {
              return functions.getBase64(item.originFileObj);
            });
            Promise.all(promises)
              .then(files => {
                let form = {
                  process: {
                    id: aux[0],
                    name: selected.name,
                  },
                  engine: nameEng,
                  source: {
                    id: aux[0],
                    name: selected.name,
                  },
                  files: files,
                  email: values.email,
                };
                setUploading(true);

                if (typeOfUser === 2 || typeOfUser === 3) {
                  quoteFile(form)
                    .then(response => {
                      message.success(messages.default.success);
                      setUploading(false);
                      setViewNoty('success');
                      setFileList([]);
                      changeFileID(response.data.fileId);
                    })
                    .catch(error => {
                      message.error(error.message);
                      setUploading(false);
                      setViewNoty('error');
                    });
                } else {
                  processFile(form)
                    .then(response => {
                      message.success(messages.default.success);
                      setUploading(false);
                      setViewNoty('success');
                      setFileList([]);
                      changeFileID(response.data.fileId);
                    })
                    .catch(error => {
                      message.error(error.message);
                      setUploading(false);
                      setViewNoty('error');
                    });
                }
              })
              .catch(err => {
                message.error(err.message);
              });
          } else {
            setShowError(true);
          }
        } else {
          const form = {
            engine: nameEng,
            text: values.text,
          };

          try {
            setTranslating(true);
            const response = await translate({ ...form });
            setResult(response.data.data[0][0].tgt);
            setTranslating(false);
          } catch (error) {
            setTranslating(false);
            message.error(error.message);
          }
        }
      }
    });
  };

  const handleChange = value => {
    const {
      selectProcess,
      process: { engines },
    } = props;

    let selected = null;
    let array = [];

    engines.forEach(element => {
      if (element.name === value.toLowerCase()) {
        selectProcess(element);
        selected = element;
      }
    });

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source,
            });
          }
        });
        if (array.length) {
          setDefaultSourceValue(array[0].name);
          setSource(array[0].source);
          //element["key"] = `${element.id}-${element.target}`;
          setTarget(`${array[0].id}-${array[0].target}`);
          setDefaultTargetValue(getTargets(selected.engines, array[0].source)[0].name);
        }
      }
    }
  };

  const changeSource = source => {
    setSource(source);
  };

  const changeTarget = target => {
    setTarget(target);
  };

  const getSources = () => {
    const {
      process: {
        selected: { engines },
      },
    } = props;

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.source) {
          flag = true;
          break;
        }
      }
      if (!flag) {
        let key = element.source;
        array.push({ key, name: functions.languages[key] });
      }
    });
    return array;
  };

  const onChange = e => {
    setRadioSelect(e.target.value);
  };

  const getTargets = (engines = null, _source = null) => {
    if (!engines) {
      engines = props.process.selected.engines;
    }
    if (!_source) {
      _source = source;
    }

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.target) {
          flag = true;
          break;
        }
      }

      if (_source) {
        if (!flag && element.source === _source) {
          if (element.domain) {
            element['key'] = `${element.id}-${element.target}`;
            element['text'] = `${functions.languages[element.target]} - ${element.name} - ${
              element.domain
            }`;
          } else {
            element['key'] = `${element.id}-${element.target}`;
            element['text'] = `${functions.languages[element.target]} - ${element.name}`;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  const renderProcess = () => {
    const {
      process,
      form: { getFieldDecorator, getFieldsError },
    } = props;

    const dprops = {
      beforeUpload: file => {
        const isLt2M = file.size / 1024 / 1024 < 20;
        if (!functions.checkFile(file, props.allowedFiles)) {
          message.error(
            messages.process.error.uploadOne.replace('allowedFiles', props.allowedFiles)
          );
        } else if (!isLt2M) {
          message.error(messages.process.error.uploadTwo);
        }
        return false;
      },
      onChange: info => {
        let aux = [];
        info.fileList.map(item => {
          if (functions.checkFile(item, props.allowedFiles)) {
            aux.push(item);
          }
          return aux;
        });

        setFileList(aux);
      },
      multiple: true,
      fileList,
      showUploadList: true,
    };

    const targets = getTargets();
    let flag = false;
    for (let index = 0; index < targets.length; index++) {
      if (targets[index].text === defaultTargetValue) {
        flag = true;
        break;
      }
    }
    if (targets.length && !flag) {
      setDefaultTargetValue(targets[0].text);
    }

    if (defaultValue) {
      if (viewNoty === 'process') {
        const options = process.engines.map(d => (
          <Option key={d.id} value={d.name}>
            {d.name.replace(/^\w/, c => c.toUpperCase())}
          </Option>
        ));

        return (
          <div>
            <Row>
              <Col>
                <ButtonWrapper
                  type="primary"
                  icon="rollback"
                  onClick={() => {
                    changeView('list');
                  }}
                >
                  {messages.process.listProcess}
                </ButtonWrapper>
              </Col>
            </Row>
            <Row>
              <Col>
                <Form onSubmit={handleSubmit}>
                  <Row type="flex" justify="center">
                    <ColWrapper xs={24}>
                      <Radio.Group onChange={onChange} value={radioSelect}>
                        <Radio value={1}>Upload File</Radio>
                        <Radio value={0}>Translate Text</Radio>
                      </Radio.Group>
                    </ColWrapper>
                    <Divider />
                    <ColWrapper xs={24} md={12}>
                      <Row type="flex" justify="center" align="middle">
                        <Col xs={24}>
                          <FormItemWrapper label={messages.process.process} hasFeedback>
                            {getFieldDecorator('process', {
                              initialValue: props.defaultValue,
                              disabled: source === null || processEnd,
                              rules: [
                                {
                                  required: true,
                                  message: messages.process.rules.process.required,
                                },
                              ],
                            })(
                              <Select autofocus="true" onChange={handleChange}>
                                {options}
                              </Select>
                            )}
                          </FormItemWrapper>
                          <FormItemWrapper label={messages.process.source} hasFeedback>
                            {getFieldDecorator('source', {
                              initialValue: defaultSourceValue,
                              disabled: processEnd,
                              rules: [
                                {
                                  required: true,
                                  message: messages.process.rules.source.required,
                                },
                              ],
                            })(
                              <Select disabled={!getSources().length} onChange={changeSource}>
                                {getSources().map(element => {
                                  return <Option key={element.key}>{element.name}</Option>;
                                })}
                              </Select>
                            )}
                          </FormItemWrapper>
                          <FormItemWrapper label={messages.process.target} hasFeedback>
                            {getFieldDecorator('target', {
                              initialValue: defaultTargetValue,
                              disabled: source === null || processEnd,
                              rules: [
                                {
                                  required: true,
                                  message: messages.process.rules.target.required,
                                },
                              ],
                            })(
                              <Select
                                onChange={changeTarget}
                                disabled={source === null || processEnd}
                              >
                                {targets.map(element => {
                                  return <Option key={element.key}>{element.text}</Option>;
                                })}
                              </Select>
                            )}
                          </FormItemWrapper>
                        </Col>
                      </Row>
                    </ColWrapper>
                    <ColWrapper xs={24} md={12}>
                      <Row>
                        <Col xs={24}>
                          {radioSelect === 1 && (
                            <FormItemWrapper>
                              <Div error={showError && !fileList.length}>
                                <Dragger {...dprops}>
                                  <p className="ant-upload-drag-icon">
                                    <Icon type="inbox" />
                                  </p>
                                  <p className="ant-upload-text">{messages.process.drag}</p>
                                </Dragger>

                                {showError && !fileList.length ? (
                                  <Alert
                                    style={{ margin: 5 }}
                                    message={messages.process.alert}
                                    type="error"
                                    showIcon
                                  />
                                ) : null}
                              </Div>
                            </FormItemWrapper>
                          )}
                          {radioSelect === 0 && (
                            <React.Fragment>
                              <FormItemWrapper style={{ marginTop: 30 }} hasFeedback>
                                {getFieldDecorator('text', {
                                  rules: [
                                    {
                                      required: true,
                                    },
                                  ],
                                })(<TextArea autosize={{ minRows: 3, maxRows: 5 }} />)}
                              </FormItemWrapper>
                              <FormItemWrapper>
                                <Paragraph
                                  style={{
                                    backgroundColor: '#f0f2f5',
                                    padding: '0 10px',
                                  }}
                                >
                                  {result}
                                </Paragraph>
                              </FormItemWrapper>
                            </React.Fragment>
                          )}
                        </Col>
                      </Row>
                    </ColWrapper>
                  </Row>
                  <Row>
                    <Col xs={24}>
                      {radioSelect === 1 && (
                        <FormItemWrapper
                          style={{
                            display: 'flex',
                            justifyContent: 'center',
                          }}
                        >
                          {!processEnd ? (
                            <Button
                              style={{ marginTop: 20 }}
                              icon="file"
                              type="primary"
                              htmlType="submit"
                              size="large"
                              loading={uploading}
                              disabled={
                                !getSources().length ||
                                !target ||
                                !source ||
                                functions.hasErrors(getFieldsError())
                              }
                            >
                              {uploading
                                ? messages.process.uploading
                                : messages.process.uploadingLoad}
                            </Button>
                          ) : null}
                        </FormItemWrapper>
                      )}
                      {radioSelect === 0 && (
                        <FormItemWrapper
                          style={{
                            display: 'flex',
                            justifyContent: 'center',
                          }}
                        >
                          {!processEnd ? (
                            <Button
                              style={{ marginTop: 20 }}
                              icon="global"
                              type="primary"
                              htmlType="submit"
                              size="large"
                              loading={translating}
                            >
                              {translating
                                ? messages.process.translating
                                : messages.process.translate}
                            </Button>
                          ) : null}
                        </FormItemWrapper>
                      )}
                    </Col>
                  </Row>
                </Form>
              </Col>
            </Row>
            <ModalWrapper
              title="Files List"
              onCancel={hideListFile}
              visible={visible}
              footer={null}
              closable
            >
              <div className="ant-upload-list ant-upload-list-text">
                {fileList.map(item => {
                  return (
                    <div
                      key={item.uid}
                      className="ant-upload-list-item ant-upload-list-item-undefined"
                    >
                      <div className="ant-upload-list-item-info">
                        <span>
                          <Icon type="paper-clip" />
                          <span className="ant-upload-list-item-name" title={item.name}>
                            {item.name}
                          </span>
                        </span>
                      </div>
                      <Icon
                        onClick={() => {
                          onRemove(item);
                        }}
                        type="close"
                      />
                    </div>
                  );
                })}
              </div>
            </ModalWrapper>
          </div>
        );
      }
      if (viewNoty === 'error') {
        return (
          <div>
            <Row>
              <Col>
                <ButtonWrapper
                  type="primary"
                  icon="rollback"
                  onClick={() => {
                    changeView('list');
                  }}
                >
                  {messages.process.listProcess}
                </ButtonWrapper>
              </Col>
            </Row>
            <Row>
              <Col>
                <DivError>
                  <ErrorCmp messages={messages} />
                  <DivButtons>
                    <Button
                      onClick={() => {
                        initStateView('process');
                      }}
                      type="primary"
                      size="large"
                    >
                      {messages.process.tryAgain}
                    </Button>
                  </DivButtons>
                </DivError>
              </Col>
            </Row>
          </div>
        );
      }
      if (viewNoty === 'success') {
        return (
          <div>
            <Row>
              <Col>
                <ButtonWrapper
                  type="primary"
                  icon="rollback"
                  onClick={() => {
                    initStateView('list');
                  }}
                >
                  {messages.process.listProcess}
                </ButtonWrapper>
              </Col>
            </Row>
            <Row>
              <Col>
                <DivError>
                  <SuccessCmp messages={messages} />
                  <DivButtons>
                    <Button
                      onClick={() => {
                        initStateView('process');
                      }}
                      type="primary"
                      size="large"
                    >
                      {messages.process.another}
                    </Button>
                  </DivButtons>
                </DivError>
              </Col>
            </Row>
          </div>
        );
      }
    } else {
      return (
        <Alert
          message={messages.process.loadingData}
          description={messages.process.loadingDataDes}
          type="info"
        />
      );
    }
  };

  const hideListFile = () => {
    setVisible(false);
  };

  const onRemove = file => {
    if (!processEnd) {
      let aux = [];
      fileList.map(item => {
        if (item.uid !== file.uid) {
          aux.push(item);
        }
        return aux;
      });
      if (!aux.length) hideListFile();
      setFileList(aux);
    }
  };

  return (
    <div>
      {props.children}
      <Breadcrumb style={{ margin: '16px 0' }}>
        {newProcess === true && <Breadcrumb.Item>{messages.process.newProcess}</Breadcrumb.Item>}
        {newProcess === false && (
          <Breadcrumb.Item>{messages.menus.processServices}</Breadcrumb.Item>
        )}
      </Breadcrumb>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        {view === 'list' ? (
          <ProcessList
            engines={props.process.engines}
            userLogin={userLogin}
            changeView={changeView}
          />
        ) : (
          renderProcess()
        )}
      </div>
    </div>
  );
};

const WrappedProcessServicesCmp = Form.create()(injectIntl(ProcessServicesCmp));

export default WrappedProcessServicesCmp;
